import {
	mapState,
	mapMutations
} from 'vuex';
import sunUiUpimg from '@/components/sunui-upimg/sunui-upimg.vue';
import mInput from '../../components/m-input.vue'
import sunTab from '@/components/sun-tab/sun-tab.vue';
import uniNavBar from "@/components/uni-nav-bar/uni-nav-bar.vue"
import service from '../../service.js';
const BASE_URL = 'http://www.luominus.com/';
const IMG_URL = 'http://www.luominus.com/static/frontend/images/upload.png';
import uniRequest from 'uni-request';
// import toUpImg from '../../upImg.js';
export default {
	computed: mapState(['forcedLogin', 'hasLogin', 'userName','avatarUrl']),
	components: {
		sunTab,mInput,uniNavBar,'sunui-upimg': sunUiUpimg
	},
	data() {
		return {
			swiperIndex:0,
			index: 0,
			account: '',
			tabSwiperList: ['支付宝','微信',],
			treatmentStatu: { checked: false },
			upimg_move: false,
			imageSrc: false,
			menus : {
						"wx": {
				            "img": IMG_URL,
				            "price": 0,
				            "count": 0,
				            "status": 0
				        },
				        "zfb": {
				            "img":IMG_URL,
				            "price": 0,
				            "count": 0,
				            "status": 0
				        },
				        "user_name": "",
				        "user_mobile": ""
			},
			
		}
	},
	computed: {
		...mapState(['hasLogin', 'forcedLogin'])
	},
	onUnload() {
		this.imageSrc = false;
	},
	onLoad() {
		var that = this
			if (!this.hasLogin) {
				uni.showLoading({
				    title: '加载中'
				});
					let validUser = service.getUsers();
					if (validUser.length !== 0) {
						const newData = {
							token:validUser[0].token
							
						}
						// 存在缓存数据getUsers（）对象
						// console.log(validUser[0].token)
						
						setTimeout(function () {
						    uni.hideLoading();
						}, 2000);
						uni.request({  
							url:BASE_URL + "api/v1/Index/getMyCode",  
							data: newData,  
							method:'GET',  
							dataType:'json',  
							header:{  
								'content-type':'application/json'  
							},  
							success: (e) =>{  
								console.log(e) 
								 if (e.statusCode === 200) {
								 		var myData = e.data.data
								 		that.menus = {
								 			wx : e.data.data.wx,
											"zfb":e.data.data.zfb,
											"user_name": e.data.data.user_name,
											"user_mobile": e.data.data.user_mobile
								 		}
										that.imageSrc = true
										if (myData.status === 1) {
											//1 未开工
											that.treatmentStatu = { checked: false }
										}
										else{
											that.treatmentStatu = { checked: true }
										}
								 		console.log(that.menus)
								 	
								 }
							},  
						})
					} else {
					uni.showModal({
						title: '未登录',
						content: '您未登录，需要登录后才能继续',
						/**
						 * 如果需要强制登录，不显示取消按钮
						 */
						showCancel: !this.forcedLogin,
						success: (res) => {
							if (res.confirm) {
								/**
								 * 如果需要强制登录，使用reLaunch方式
								 */
								if (this.forcedLogin) {
									uni.reLaunch({
										url: '../login/login'
									});
								} else {
									uni.navigateTo({
										url: '../login/login'
									});
								}
							}
						}
					});
				}		
			}
		}
	,
	//退出登录
	onNavigationBarButtonTap(e) {
	    console.log("success")  
		uni.removeStorage({
		    key: 'USERS_KEY',
		    success: function (res) {
		        console.log('success');
		    }
		});
		  uni.navigateTo({
			url: '../login/login',
		  });
	},
	methods: {
		...mapMutations(['logout']),
		chooseImageZFB: function(index) {
			var that = this
				uni.chooseImage({
					count: 1,
					sizeType: ['compressed'],
					sourceType: ['album'],
					success: (res) => {
						console.log('chooseImage success, temp path is', res.tempFilePaths[0])
						var imageSrc = res.tempFilePaths[0]
						
						uni.uploadFile({
							url: BASE_URL+ "api/v1/Index/uploadImg",
							filePath: imageSrc,
							fileType: 'image',
							name: 'file',
							success: (res) => {
								console.log('uploadImage success, res is:', res)
								that.imageSrc = true
								that.menus.zfb.img = imageSrc
								let validUser = service.getUsers();
								var upPicData = {
									code: that.imageSrc ,
									type: 1,
									token:validUser[0].token,
								}
								console.log(upPicData)
								uniRequest.post(BASE_URL + "api/v1/User/addCode", upPicData)
									.then(function(response) {
										console.log(response);
										if (response.status === 200) {
											uni.showToast({
												title: '上传成功',
												icon: 'success',
												duration: 1000
											})
										}
										else {
											// uni.showToast({
											// 	icon: 'none',
											// 	title: '用户账号或密码不正确',
											// });
										}
									}).catch(function(error) {
										console.log(error);
									});
								
							},
							fail: (err) => {
								console.log('uploadImage fail', err);
								uni.showModal({
									content: err.errMsg,
									showCancel: false
								});
							}
						});
					},
					fail: (err) => {
						console.log('chooseImage fail', err)

						uni.getSetting({
							success: (res) => {
								let authStatus = res.authSetting['scope.album'];
								if (!authStatus) {
									uni.showModal({
										title: '授权失败',
										content: 'Hello uni-app需要从您的相册获取图片，请在设置界面打开相关权限',
										success: (res) => {
											if (res.confirm) {
												uni.openSetting()
											}
										}
									})
								}
							}
						})

					}
				})
			},
		
		chooseImageWX: function(index) {
			var that = this
				uni.chooseImage({
					count: 1,
					sizeType: ['compressed'],
					sourceType: ['album'],
					success: (res) => {
						console.log('chooseImage success, temp path is', res.tempFilePaths[0])
						var imageSrcMy = res.tempFilePaths[0]
						uni.uploadFile({
							url: BASE_URL+ "api/v1/Index/uploadImg",
							filePath: imageSrcMy,
							fileType: 'image',
							name: 'file',
							success: (res) => {
								console.log('uploadImage success, res is:', res)
								that.imageSrc = true
								that.menus.wx.img = imageSrcMy
								let validUser = service.getUsers();
								var upPicData = {
									code: that.menus.wx.img,
									type: 2,
									token:validUser[0].token,
								}
								console.log(upPicData)
								// uniRequest.post(BASE_URL + "api/v1/User/addCode", upPicData)
								// 	.then(function(response) {
								// 		console.log(response);
								// 		if (response.status === 200) {
											
								// 			uni.showToast({
								// 				title: '上传成功',
								// 				icon: 'success',
								// 				duration: 1000
								// 			})
								// 		}
								// 		else {
								// 			// uni.showToast({
								// 			// 	icon: 'none',
								// 			// 	title: '用户账号或密码不正确',
								// 			// });
								// 		}
								// 	}).catch(function(error) {
								// 		console.log(error);
								// 	});
								
							},
							fail: (err) => {
								console.log('uploadImage fail', err);
								uni.showModal({
									content: err.errMsg,
									showCancel: false
								});
							}
						});
					},
					fail: (err) => {
						console.log('chooseImage fail', err)

						uni.getSetting({
							success: (res) => {
								let authStatus = res.authSetting['scope.album'];
								if (!authStatus) {
									uni.showModal({
										title: '授权失败',
										content: 'Hello uni-app需要从您的相册获取图片，请在设置界面打开相关权限',
										success: (res) => {
											if (res.confirm) {
												uni.openSetting()
											}
										}
									})
								}
							}
						})

					}
				})
			},
		
		
		bindLogin() {
			uni.navigateTo({
				url: '../login/login',
			});
		},
		bindLogout() {
			this.logout();
			/**
			 * 如果需要强制登录跳转回登录页面
			 */
			if (this.forcedLogin) {
				uni.reLaunch({
					url: '../login/login',
				});
			}
		},
		swiperChange(e){
			this.swiperIndex = e.target.current;
		},
		showOrNo(img) {
			// console.log(img)
			if (img === 0) {
					return true;
			}
			else if(img === 1)
			{
				return false;
			}
		},
		img(img) {
			// console.log(img)
			// console.log(img === IMG_URL)
			return (img !== IMG_URL);
		},
		switchChange (e) {
			let value = e.target.value
			let that = this
			this.$set(this.treatmentStatu, 'checked', value)   // 将点击改变的状态赋给treatmentStatu.checked
			console.log(value)
			console.log(!this.havePromise)
			var  newStatus = 1
			if (value && !this.havePromise) {
				newStatus =2
				that.$set(that.treatmentStatu, 'checked', false)  // 手动修改switch的状态，视图会同步更新
				
			}
			let validUser = service.getUsers();
			const data = {status:newStatus,token:validUser[0].token}
			uniRequest.post(BASE_URL + "api/v1/Index/changeWorkStatus", data)
				.then(function(response) {
					if (response.status === 200) {
						console.log(response);
						if (response.data.code === 200) {
							console.log('cg');
							
						}
					}
					else {
						uni.showToast({
							icon: 'none',
							title: '请稍候重试',
						});
					}
				}).catch(function(error) {
					console.log(error);
				});
		}
	}
}
